.Navbar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 10vh;
	padding: 0 2rem;
}

.Navbar__logo {
	width: 48px;
	height: 48px;
	cursor: pointer;
}

.Navbar__menu {
	width: 24px;
	height: 24px;
	cursor: pointer;
}

.Navbar__image {
	width: 100%;
	display: block;
}

