.Writing__container {
	height: calc(90vh - 4rem);
	margin: 2rem 5rem 0 8rem;
	display: flex;
	gap: 3rem;
}

.Writing__featured {
	flex-basis: 65%;
	height: 100%;

	display: flex;
	flex-direction: column;
	padding: 0 3rem 0 2rem;
}

.Writing__asideNav {
	flex-basis: 35%;
	width: 100%;
	background-color: #be8b8b;
	height: 100%;
}

.Writing__fl {
	padding: 10px;
	width: 100%;
	display: flex;
	flex-direction: column;
	margin-bottom: 2rem;
	background-color: #f9f9f9;
}

.Writing__fl-heading {
	margin-bottom: 1.8rem;
}

.Writing__fl-content {
	margin-bottom: 1.8rem;
}

.Writing__fl-readMore {
	margin-bottom: 1.8rem;
	width: 20%;
	padding: 0.5rem;
	align-self: flex-end;
}

@media only screen and (max-width: 1081px) {
	.Writing__container {
		height: auto;
		flex-direction: column;
		margin: 0;
	}

	.Writing__featured {
		flex-basis: 100%;
		padding: 1rem;
	}

	.Writing-fs {
		display: none;
	}

	.Writing__asideNav {
		flex-basis: 100%;
	}

	.Writing__fl-readMore {
		width: 50%;
		padding: 0.5rem;
	}
}
