.Homepage {
	width: 100%;
	height: 90vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.Homepage__message {
	display: flex;
	justify-content: center;
	align-items: center;
}

@media only screen and (max-width: 1081px) {
	.Homepage__message {
		flex-direction: column;
	}
	.Homepage__message-fs {
		display: none;
	}
}
