.Menu {
	height: 100vh;
	background-color: #3aa1c1;
	display: flex;
	flex-direction: column;
}

.Menu__header {
	display: flex;
	justify-content: flex-end;
	padding: 4rem 4rem 0;
}

.Menu__close-button {
	scale: 0.75;
	display: block;
	cursor: pointer;
	padding: 16px;
}

.Menu__close-button:hover {
	background-color: #ffffff2f;
	border-radius: 100%;
	padding: 16px;
}

.Menu__links {
	display: flex;
	padding: 6rem 8rem 0;
	gap: 3rem;
}

.Menu__link {
	color: white;
	text-decoration: none;
	padding: 18px;
}

.Menu__link:hover {
	background-color: rgba(255, 255, 255, 0.2);
	padding: 18px;
	border-radius: 18px;
}

.Menu__submenu {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	padding: 0 calc(8rem + 18px);
	margin-top: 1rem;
}

@media only screen and (max-width: 600px) {
	.Menu__links {
		flex-direction: column;
		padding: 1.25rem 1.5rem 0;
		gap: 1rem;
	}

	.Menu__submenu {
		padding: 0 calc(1.5rem + 18px);
	}
}
